<template>
  <div class="form-edit">
    <header>
      <span class="title"></span>
      <el-popover
        placement="left"
        width="310"
        trigger="hover"
        popper-class="add-field"
      >
        <el-button type="primary" size="mini" slot="reference">
          
        </el-button>
        <div class="add-field__btns">
          <el-tag @click="addItem('text')"></el-tag>
          <el-tag @click="addItem('switch')"></el-tag>
          <el-tag @click="addItem('radio')"></el-tag>
          <el-tag @click="addItem('checkbox')"></el-tag>
          <el-tag @click="addItem('stepper')"></el-tag>
          <el-tag @click="addItem('rate')"></el-tag>
          <el-tag @click="addItem('picker')"></el-tag>
          <el-tag @click="addItem('datetimePicker')"></el-tag>
        </div>
      </el-popover>
    </header>
    <form-edit-item
      v-for="(item, idx) in form"
      :key="idx"
      :form="form"
      :formItem="item"
      :idx="idx"
    >
    </form-edit-item>
  </div>
</template>

<script>
import FormEditItem from "@/components/operating-floor/form-edit-item";

export default {
  name: "FormEdit",
  components: {
    FormEditItem
  },
  props: {
    form: {
      type: Array,
      default: () => []
    }
  },
  methods: {
    /**
     * 
     * @param {string} type
     */
    addItem(type) {
      const defaultConfig = {
        text: {
          label: "",
          value: "",
          type: "text",
          typeName: ""
        },
        switch: {
          label: "",
          value: "",
          type: "switch",
          typeName: ""
        },
        radio: {
          label: "",
          value: "",
          type: "radio",
          typeName: "",
          options: ["1", "2"]
        },
        checkbox: {
          label: "",
          value: [],
          type: "checkbox",
          typeName: "",
          options: ["1", "2"]
        },
        stepper: {
          label: "",
          value: 1,
          type: "stepper",
          typeName: ""
        },
        rate: {
          label: "",
          value: 1,
          type: "rate",
          typeName: ""
        },
        picker: {
          label: "",
          value: "",
          type: "picker",
          typeName: "",
          options: ["1", "2"]
        },
        datetimePicker: {
          label: "",
          value: "",
          type: "datetimePicker",
          typeName: ""
        }
      };
      this.form.push(defaultConfig[type]);
    }
  }
};
</script>
